.adminContainer {
  max-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  color: white;
  z-index: 1;
}

.applicantContainer {
  padding: 20vh 20vw;
  width: 100vw;
}

.approveButton {
  margin: 10px;
}

.applicantsHeader {
  padding: 10vh 0vw;
}
.cryptoDisplay {
  width: 70vw;
  font-size: 1.3rem;
  border: 2px solid white;
  padding: 10px;
  margin-bottom: 100px;
}

.tableHeader {
  border-bottom: 2px solid white;
  margin: 10px 0px;
}

.applicantRowItem {
  text-align: left;
  border-bottom: 2px solid white;
}
.openButton:hover {
  font-size: 1.4rem;
  background-color: rgb(5, 68, 149);
}

.openButton {
  margin: 10px;
}
.applicantSelected {
  background-color: greenyellow !important;
  color: black;
  border: 1px solid white;
}

.applicantProgressRow {
  border: 2px solid white;
}
