.createPostPage {
  padding-top: 10vh;
}

.registrationPage {
  background-color: #04161d;
  color: rgb(255, 255, 255);
  width: 100vw;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 100px;
}
.formContainer {
  width: 60vw;
}
.inputGp {
  padding: 3vh 0vw;
  display: flex;
  flex-direction: column;
  font-size: 1.2rem;
}

.submitButton {
  color: white;
  font-size: 1.4rem;
  font-weight: 600;
  background-color: rgb(39, 109, 200);
  border: none;
  width: 100%;
  min-height: 50px;
  border-radius: 6px;
  margin: auto;
  margin-bottom: 100px;
}

.submitButton:hover {
  font-size: 1.43rem;
  font-weight: 620;
  min-height: 52px;
}

.inputlabel {
  margin-bottom: 10px;
}
