.navbar {
  margin: 0;
  width: 100%;
  max-width: 100vw;
  min-height: 80px;
  padding: 0px;
  background-color: #01212e;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  text-decoration: none;
}
.navbar .link {
  color: white;
  font-size: 1.2rem;
}

.navbar .link:hover {
  color: rgb(207, 207, 207);
  font-size: 1.4rem;
}

.link img {
  max-height: 80px;
  padding-left: 20px;
}

nav div {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 50%;
}

nav button {
  width: 80px;
  font-size: 1.2rem;
  font-weight: 500;
  background-color: #04161d;
  color: white;
  box-shadow: none;
  border-radius: 5px;
  border: none;
  padding: 5px 0px;
  margin: auto;
}
nav button:hover {
  cursor: pointer;
  background-color: #0e5d7c;
}
