.homePage {
  max-width: 100vw;
  min-height: 100vh;
  height: auto;
  display: flex;
  flex-direction: column;
  background-color: #011821;
  color: rgb(255, 255, 255);
  align-items: center;
  padding-top: 10vh;
}

.aboutDarwin p {
  color: rgb(255, 255, 255) !important;
  text-align: justify;
  font-size: 1.1rem;
  padding-top: 3vh;
  max-width: 80vw;
}

.Banner {
  min-width: 350px;
  height: auto;
  opacity: 0.75;
  margin-top: 10vh;
  width: 70vw;
  border-radius: 8px;
}

.registerButton {
  color: black;
  min-width: 180px;
  font-size: 1.3rem;
  background-color: white;
  font-weight: 600;
  height: auto;
  padding: 20px;
  border-radius: 7px;
  text-align: center;
  text-decoration: none;
  margin: 6vh;
}

.registerButtonOrange {
  color: black;
  min-width: 180px;
  font-size: 1.3rem;
  background-color: orangered;
  font-weight: 600;
  height: auto;
  padding: 20px;
  border-radius: 7px;
  text-align: center;
  text-decoration: none;
  margin: 6vh;
}

.slotBookingContainer {
  text-align: center;
}
