body {
  background-color: #011821 !important;
}
.navbarSide {
  overflow-x: hidden;
  background-color: black;
  position: fixed;
  display: flex;
  flex-direction: column;
  color: rgb(255, 255, 255);
  justify-content: space-around;
  align-items: center;
  height: 100%;
  min-height: 100vh;
}
.navItem {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  background-color: #000000;
}

.navItemTitle {
  color: rgb(255, 255, 255);
  font-size: 1.4rem;
  font-weight: 600;
  background-color: #000000;
  width: 100%;
  text-align: center;
  text-decoration: none;
}
.navItemTitle:hover {
  color: rgb(209, 204, 204);
  font-size: 1.5rem;
  font-weight: 600;
}

.navItem button {
  background-color: #000000;
  color: white;
  font-size: 1.4rem;
  font-weight: 600;
  box-shadow: none;
  border: none;
  border-radius: 5px;
}
.navItem button:hover {
  cursor: pointer;
  background-color: #000000;
  font-size: 1.5rem;
}

@media (min-width: 900px) {
  .navbarSide {
    width: 15vw;
    min-width: 150px;
    border-right: 5px solid rgb(255, 255, 255);
  }
  .bars {
    display: none;
  }
}
@media (max-width: 900px) {
  .navbarSide {
    width: 100%;
    min-width: 100vw;
    max-width: 100vw;
  }
  .bars i {
    display: block;
    width: 100vw;
  }
}

.bars {
  background-color: #011821;
  color: white;
  width: 15vw;
  min-width: 150px;
  text-align: center;
  font-size: 2rem;
  padding-top: 10px;
}
.bars i {
  background-color: #011821;
}

.SidebarContainer {
  background-color: #011821;
  max-width: 100vw;
}
