.detailsContainer {
  min-height: 100vh;
  overflow-x: hidden;
  color: white;
  z-index: 1;
}

.details {
  width: 100vw;
  max-width: 100vw;
}

.userLogo {
  width: 20vw;
  height: auto;
}

.detailsTable {
  width: 60vw;
  max-width: 100vw;
  font-size: 1.2rem;
  margin: 5vh 0vw;
}

.detailsTableRow {
  width: 100%;
  max-width: 100vw;
}

.detailsTableItem {
  border: 2px solid white;
  width: 50%;
  height: auto;
  padding: 5vh;
}

@media (min-width: 900px) {
  .details {
    padding: 20vh 20vw;
  }
}
