.slotBookingPage {
  display: flex;
  width: 100vw;
  max-width: 100vw;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
  padding-top: 5vh;
}
.slotsHeading {
  display: block;
  width: 100vw;
  max-width: 100vw;
}
.slots {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  min-height: 80vh;
  max-width: 100vw;
  padding: 10vh;
}

.slotRow {
  display: flex;
  width: 100%;
  max-width: 80vw;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.bookingSlot {
  color: black;
  min-width: 100px;
  width: 15vw;
  font-size: 1.3rem;
  background-color: orangered;
  font-weight: 600;
  height: auto;
  min-height: 15vh;
  padding: 20px;
  border-radius: 7px;
  text-align: center;
  text-decoration: none;
  margin-top: 6vh;
}

.bookingSlotDisable {
  color: black;
  min-width: 100px;
  width: 15vw;
  font-size: 1.3rem;
  background-color: rgb(52, 51, 50);
  font-weight: 600;
  height: auto;
  min-height: 15vh;
  padding: 20px;
  border-radius: 7px;
  text-align: center;
  text-decoration: none;
  margin-top: 6vh;
  cursor: not-allowed;
  pointer-events: none;
}
