body {
  background-color: #fff;
}
.container {
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-width: 500px;
  width: 100%;
  margin: auto;
}
.container > form {
  width: 70%;
  border: 1px solid #dfdfdf;
  padding: 20px;
  border-radius: 5px;
  color: white;
  background-color: #003653;
  text-align: center;
}

form input {
  margin: 3vh 0;
  width: 100%;
}

button {
  background: #0563b4;
  color: rgb(255, 255, 255);
  margin-bottom: 2.5vh;
}
p {
  color: red;
}

pre {
  font-size: 2vh;
  display: block;
}
